import QvipleLoader from "../../../../Loader/QvipleLoader";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import AddHostelFeeStructure from "./AddHostelFeeStructure";
import {
  useHostelFeeStructureRemove,
  useHostelFeeStructureRetroUpdate,
  useHostelFeeStructureUpdate,
} from "../../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";

const HostelFeeStructureDetailMenu = ({
  fid,
  instituteId,
  hostelId,
  openMenu,
  onClose,
  flowAs,
  onCloseParent,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [hostelFeeStructureRemove] = useHostelFeeStructureRemove();
  const [hostelFeeStructureUpdate] = useHostelFeeStructureUpdate();
  const [hostelFeeStructureRetroUpdate] = useHostelFeeStructureRetroUpdate();
  const [edit, setEdit] = useState(false);

  const onDelete = () => {
    if (openMenu?._id && flowAs) {
      setDisabled((pre) => !pre);
      hostelFeeStructureRemove({
        flow: "Hostel_Manager",
        sid: openMenu?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onCloseParent();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val, asEdit) => {
    if (openMenu?._id) {
      setDisabled((pre) => !pre);
      setEdit(false);
      if (asEdit === "RETRO") {
        hostelFeeStructureRetroUpdate({
          sid: openMenu?._id,
          flow: "Hostel_Manager",
          bodyParameter: {
            ...val,
            hostel: hostelId,
          },
        })
          .then(() => {
            onClose();
            setDisabled((pre) => !pre);
          })
          .catch({});
      } else {
        hostelFeeStructureUpdate({
          sid: openMenu?._id,
          flow: "Hostel_Manager",
          bodyParameter: val,
        })
          .then(() => {
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    }
  };
  return (
    <>
      {!edit ? (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={onOpenEdit}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      ) : (
        <AddHostelFeeStructure
          onClose={onOpenEdit}
          fid={fid}
          openAs="EDIT"
          openStructureMenu={openMenu}
          onEdit={onEdit}
          hostelId={hostelId}
          instituteId={instituteId}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default HostelFeeStructureDetailMenu;
